<template>
	<section class="blog">
		<div class="row">
			<div class="col-12 col-lg-4" v-for='article in sortedByDateDescending' :key='article.id'>
				<article class="flip blog-article">
					<div class="edit" v-if='User'>
						<router-link :to="{ name: 'edit_post', params: { title: friendlyUrl(article.title), id: article.id }}">
							<img src="../blog/icons/edit-regular.svg" alt="Edit">
						</router-link>
						
					</div>
				    <div class="front" :style="{'background-image': `url(${article.img})`}">
				       <h5 class="blog-article__title">{{article.title}}</h5>
				    </div>
				    <div class="back">
				    	<div class="blog-article__excerpt">
				    		{{article.excerpt}}
				    	</div>
				      <router-link
				      	class="blog-article__more_button" 
				      	:to="{ name: 'single-article', params: { title: friendlyUrl(article.title), id: article.id }}"
				      >
				      	Читай далі
				      </router-link>
				    </div>
				</article>
			</div>
			<!-- /.col-12 col-lg-4 -->
		</div>
		<!-- /.row -->
	</section>
	<!-- /.blog -->
</template>
<script>
	import { db } from '@/main';
	import cyrillicToTranslit from 'cyrillic-to-translit-js';
	export default {
	  	name: "blogArticles",
	  	data () {
	  		return {
	  			articles: []
	  		}
	  	},
	  	methods: {
	  		friendlyUrl(value) {
	  			return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
			},
			excerpt(value) {
				return `${value.slice(0, 150)}...`;
	   		}
	  	},
	  	firestore () {
		 	return {
		  	 	articles: db.collection('blog')
		 	}
	   	},
	   	computed: {
			User() {
				return this.$store.getters.User;
			},
			sortedByDateDescending() {
				return this.articles.reverse();
			}
		},
	};
</script>
